body{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

button{
  outline: none;
  background: black;
  color: white;
  cursor: pointer;
}

.mainContainer{
  display: flex;
  justify-content: space-between;
  height: calc(100vh - 25px);
  width: calc(100% - 25px);
  border-radius: 0.5em;
  overflow: hidden;
  background: red;
}

.mainContainer::before{
  content: "";
  position: absolute;
  height: calc(100% - 25px);
  width: calc(100% - 25px);
  background: url("http://freedesignfile.com/upload/2015/10/Grunge-textures-vintage-background-vectors-01.png");
}

.left-section{
  width: 20%;
  height: 100%;
  background: tranparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.left-section-circle-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
}

.home-small-button{
  border: 5px solid white;
  border-radius: 50%;
  height: 55px;
  width: 55px;
  margin: 5px;
  z-index: 5;
}

.home-big-button{
  border-radius: 50%;
  border: 5px solid white;
  height: 100px;
  width: 100px;
  margin: 5px;
  z-index: 5;
}

ui-view{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60%;
  height: 100%;
}

.shapes-container{
  margin: auto;
  position: relative;
  width: 40vw;
  height: 40vw;
  border: 1px solid black;
}

.shape{
  height: 100%;
  width: 100%;
  background: white;
  position: absolute;
}

